import React, { useContext, useEffect, useState } from "react";
import { countryCode } from "../assets/country-code";
import LanguageDetect from "languagedetect";
import { useLangDispatch, QueryContext, useLang } from "./TranslateContexts";

// This component is used to set the "toLang" and "fromLang"
// ---------------------------------------------------------
function LanguageBar({ box }) {
  const lang = useLang();
  const [selectedLang, setSelectedLang] = useState("es");
  const [activeButton, setActiveButton] = useState(
    box === "source" ? "en" : "fr"
  );
  const lngDetector = new LanguageDetect();
  const dispatch = useLangDispatch();
  const { query } = useContext(QueryContext);

  useEffect(() => {
    const defaultLang = box === "source" ? "en" : "fr";
    const langValue = box === "source" ? lang.source : lang.result;
    if (langValue !== defaultLang) {
      setSelectedLang(langValue);
      setActiveButton("drop-down");
    }
  }, [lang]);

  function handleLanguage(event) {
    console.log(event.target.id);
    switch (event.target.id) {
      case "detect": {
        setActiveButton("drop-down");
        lngDetector.setLanguageType("iso2");
        const detectedLang =
          lngDetector.detect(query, 1)[0][0] || navigator.language;
        setSelectedLang(detectedLang);
        dispatch({
          type: box,
          newLang: detectedLang,
        });
        break;
      }
      case "en": {
        setActiveButton("en");
        dispatch({
          type: box,
          newLang: "en",
        });
        break;
      }
      case "fr": {
        setActiveButton("fr");
        dispatch({
          type: box,
          newLang: "fr",
        });
        break;
      }
      case "drop-down": {
        setActiveButton("drop-down");
        setSelectedLang(event.target.value);
        dispatch({
          type: box,
          newLang: event.target.value,
        });
        break;
      }
      case "switch-image": {
        setActiveButton("");
        dispatch({
          type: "switch",
        });
        break;
      }
      default: {
        dispatch({
          type: "initial",
        });
      }
    }
  }

  return (
    <div className="language-bar">
      {/* Start buttons - Detect , English, French and Drop-down  */}
      <div className="start-buttons">
        {box === "source" && (
          <button
            id="detect"
            className={`dm-sans medium language-button ${
              activeButton === "detect" && "active"
            }`}
            onClick={handleLanguage}
          >
            Detect language
          </button>
        )}

        <button
          id="en"
          className={`dm-sans medium language-button ${
            activeButton === "en" && "active"
          }`}
          onClick={handleLanguage}
        >
          English
        </button>

        <button
          id="fr"
          className={`dm-sans medium language-button ${
            activeButton === "fr" && "active"
          }`}
          onClick={handleLanguage}
        >
          French
        </button>

        {/* Drop down list of languages */}
        <select
          id="drop-down"
          className={`dm-sans medium language-button ${
            activeButton === "drop-down" && "active"
          }`}
          value={selectedLang}
          onChange={handleLanguage}
          onClick={handleLanguage}
        >
          {countryCode.map((country) => {
            return (
              <option key={country.id} value={country.code}>
                {country.name}
              </option>
            );
          })}
        </select>
      </div>

      {/* End buttons - Switch */}
      <div className="end-buttons">
        {box === "result" && (
          <button
            id="switch"
            onClick={handleLanguage}
            className={`square-button end-display`}
          >
            <img
              id="switch-image"
              src={`${process.env.PUBLIC_URL}/images/Horizontal_top_left_main.svg`}
              alt="switch"
            />
          </button>
        )}
      </div>
    </div>
  );
}

export default LanguageBar;
