import { useContext, useEffect, useState } from "react";
import {
  QueryContext,
  useFetch,
  ResultContext,
  useLang,
} from "./TranslateContexts";

function Translate() {
  const maxLength = 500;
  const lang = useLang();
  const { query } = useContext(QueryContext);
  const { setResult } = useContext(ResultContext);
  const [triggerFetch, setTriggerFetch] = useState("");
  const { result, error } = useFetch(triggerFetch, lang);

  useEffect(() => {
    setResult(result);
  }, [result]);

  function handleTranslate() {
    setTriggerFetch(query);
  }

  return (
    <div className="translate">
      <p className="dm-sans small">
        {query.length}/{maxLength}
      </p>
      <button className="blue-button dm-sans bold" onClick={handleTranslate}>
        <img
          src={`${process.env.PUBLIC_URL}/images/Sort_alfa.svg`}
          alt="A underlined"
        />{" "}
        Translate
      </button>
    </div>
  );
}

export default Translate;
