import { useContext, useEffect } from "react";
import {
  useDebounce,
  QueryContext,
  ResultContext,
  useFetch,
  useLang,
} from "./TranslateContexts";

function TextArea({ box, children }) {
  const { result: contextResult, setResult } = useContext(ResultContext);
  const { query, setQuery } = useContext(QueryContext);
  const maxLength = 500;
  const lang = useLang();

  const debounceValue = useDebounce(query, 1000); // debounceValue is the query delayed by 1000ms
  const { result, error } = useFetch(debounceValue, lang);

  useEffect(() => {
    if (result) {
      setResult(result);
    }
  }, [result, setResult]);

  function handleSetQuery(event) {
    if (event.target.value.length <= maxLength) {
      setQuery(event.target.value);
    }
  }

  return (
    <div className="text-area">
      <textarea
        onChange={handleSetQuery}
        className={"dm-sans medium"}
        rows={4}
        cols={50}
        value={box === "source" ? query : contextResult}
      ></textarea>
      {children}
    </div>
  );
}

export default TextArea;
