import {
  createContext,
  useContext,
  useReducer,
  useState,
  useEffect,
} from "react";

// Create contexts
const LangContext = createContext(null);
const LangDispatchContext = createContext(null);

// Context shared across all components for single truth point
export const QueryContext = createContext(null);
export const ResultContext = createContext(null);

export function LangProvider({ children }) {
  const [lang, dispatch] = useReducer(langReducer, initialLang);
  const [query, setQuery] = useState("Hello, how are you?");
  const [result, setResult] = useState("");

  return (
    <LangContext.Provider value={lang}>
      <LangDispatchContext.Provider value={dispatch}>
        <QueryContext.Provider value={{ query, setQuery }}>
          <ResultContext.Provider value={{ result, setResult }}>
            {children}
          </ResultContext.Provider>
        </QueryContext.Provider>
      </LangDispatchContext.Provider>
    </LangContext.Provider>
  );
}

export function useLang() {
  return useContext(LangContext);
}

export function useLangDispatch() {
  return useContext(LangDispatchContext);
}

function langReducer(lang, action) {
  switch (action.type) {
    case "source": {
      return { ...lang, source: action.newLang };
    }
    case "result": {
      return { ...lang, result: action.newLang };
    }
    case "switch": {
      console.log(lang);
      const newSource = lang.result;
      const newResult = lang.source;
      return { source: newSource, result: newResult };
    }
    case "initial": {
      return { lang };
    }
    default: {
      console.log(action);
      throw Error("Unknown action: " + action.type);
    }
  }
}

// The initial setup is from english to french
const initialLang = {
  source: "en",
  result: "fr",
};

// Debounce Hook. To delay the API calls
export function useDebounce(cb, delay) {
  const [debounceValue, setDebounceValue] = useState(cb);
  useEffect(() => {
    const handler = setTimeout(() => {
      setDebounceValue(cb);
    }, delay);

    return () => {
      clearTimeout(handler);
    };
  }, [cb, delay]);

  return debounceValue;
}

// Fetch Hook. Get the actual data. Triggers the api call if there is a change in the query or lang
export function useFetch(query, lang) {
  const [result, setResult] = useState("");
  const [error, setError] = useState(null);

  useEffect(() => {
    const controller = new AbortController();
    const { signal } = controller;

    async function fetchData() {
      try {
        const response = await fetch(
          `https://api.mymemory.translated.net/get?q=${query}&langpair=${lang.source}|${lang.result}`,
          { signal }
        );
        const data = await response.json();
        setResult(data.responseData.translatedText);
      } catch (err) {
        if (err.name !== "AbortError") {
          console.log(err);
          setError(err);
        }
      }
    }

    if (query) {
      fetchData();
    }

    return () => {
      controller.abort();
    };
  }, [query, lang]);

  return { result, error };
}
