import React, { useContext } from "react";
import { useLang, QueryContext, ResultContext } from "./TranslateContexts";

function AccessBar(props) {
  const lang = useLang();
  const { query } = useContext(QueryContext);
  const { result } = useContext(ResultContext);

  const speech = props.box === "source" ? query : result;

  function handleSpeech() {
    if ("speechSynthesis" in window) {
      const utterance = new SpeechSynthesisUtterance(speech);
      utterance.lang = props.box === "source" ? lang.source : lang.result;
      speechSynthesis.speak(utterance);
    } else {
      alert("Your browser does not support text-to-speech");
    }
  }

  function handleCopy() {
    navigator.clipboard
      .writeText(speech)
      .then(function () {
        alert("copied"); // success
      })
      .catch(function () {
        alert("There was some error while copying"); // error
      });
  }

  return (
    <div className="access-bar">
      <button className="square-button" onClick={handleSpeech}>
        <img
          src={`${process.env.PUBLIC_URL}/images/sound_max_fill.svg`}
          alt="sound"
        />
      </button>
      <button className="square-button" onClick={handleCopy}>
        <img src={`${process.env.PUBLIC_URL}/images/Copy.svg`} alt="copy" />
      </button>
    </div>
  );
}

export default AccessBar;
